.page {
  font-family: Roboto, sans-serif;
}

.page__body {
  grid-template-columns: 1fr;
  margin: 0;
  display: grid;
}

.page__body > * {
  grid-column: 1 / -1;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav {
  height: 60px;
  line-height: 60px;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  font-size: 12px;
  font-style: normal;
  text-decoration: none;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  text-align: center;
  color: #060b35;
  text-wrap: nowrap;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav .is-active {
  color: #00acdc;
  position: relative;
}

.nav .is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.main {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-block: 0;
  width: 100%;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.card__ratingReveiws {
  color: #060b35;
  margin-block: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__priceName {
  color: #616070;
  margin-block: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__priceAmount {
  color: #000;
  margin-block: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.button {
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s;
  display: block;
}

.button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
